export default {
    name: 'min-action-sheet',
    data () {
        return {
            show: false,
            asID: 'as',
            showCancel: true,
            cancelText: '取消',
            maskClose: true,
            actions: [],
            success: () => {},
            isClick: true
        }
    },
    methods: {
        handleShow ({showCancel = true, cancelText = '取消',
            maskClose = true, actions = [], asID = 'as',
            success = () => {}}) {
            this.show = true
            this.asID = asID
            this.showCancel = showCancel
            this.cancelText = cancelText
            this.maskClose = maskClose
            this.actions = actions
            this.success = success
        },
        handleHide () {
            this.show = false
            this.asID = 'as'
            this.showCancel = true
            this.cancelText = '取消'
            this.maskClose = true
            this.actions = []
            this.success = () => {}
            this.isClick = true
        },
        handleMaskClick () {
            if (!this.isClick) return
            this.maskClose && this.handleHide()
        },
        handleClick (id) {
            if (!this.isClick) return
            if (this.actions[id]) {
                if (this.actions[id].loading === 0) {
                    this.actions[id].loading = 1
                    this.success({asID: this.asID, id, handleHide: this.handleHide,devName:this.actions[id].name})
                    this.isClick = false
                    return
                }
            }
            this.success({asID: this.asID, id,devName:this.actions[id].name})
            this.handleHide()
        }
    }
}
